import React from 'react'
import '../CSS/Footer.css';
import {Link} from "react-router-dom";

function Footer() {
    return (
        <>
            <div id="footer">
                <div className="footerContainer-1">
                    <h2 className="textCenter fs-4">Intrest Blog</h2>
                    <div className="textCenter "><Link to="/" target="_top">Home</Link> / <Link to="/articles/" >Latest Articles</Link> / <Link to="/account-view/contact" >Contact Us</Link></div>
                    <div><Link to="/account-view/privicy-policy" >Privicy Policy</Link> / <Link to="/account-view/about" >About Us</Link> / <Link to="/account-view/contact" >User Feedback</Link></div>
                </div>
                <div className="footerContainer-2">All right reserved by Intrest Blog &copy; Copyrigh | 2021</div>
            </div>
        </>
    )
}

export default Footer
