import '../CSS/App.css';
import Articles from './Articles';
import Footer from './Footer';
import Account from './Account'
import MainLanding from './MainLanding';
import ArticleView from './ArticleView';
import Navbar from './Navbar';
import { Notifications } from './Notifications';
import {
  BrowserRouter as Router,
  Routes,
  Route,
} from "react-router-dom";
import ArticlesCetagory from './ArticlesCetagory';
import { AuthProvider } from '../Context/AuthContext';
import { SearchProvider } from '../Context/SearchContext';
import { UtilsProvider } from '../Context/UtilsContext';
import { AccountDetails } from './AccountDetails';
import { SavedList } from './SavedList';
import { ViewHistory } from './ViewHistory';
import { Contact } from './Contact';
import { PrivicyPolicy } from './PrivicyPolicy';
import { About } from './About';

function App() {

  // main colors
  // #E83B8C
  // #F48144
  // #4B49B7
  // #D79ACD

  return (
    <>
      <AuthProvider>
        <SearchProvider>
          <UtilsProvider>
            <Router>

              <Navbar />
              <div>
          
                <Routes>

                    <Route exact path="/articles/" element={<><div style={{minHeight:'100vh'}}><Articles /></div></>} />
                    <Route path="/account-view/" element={<AccountDetails />} />
                    <Route path="/account-view/saved-list" element={<><div style={{minHeight:'80vh'}}><SavedList /></div></>} />
                    <Route path="account-view/view-history" element={<><div style={{minHeight:'80vh'}}><ViewHistory /></div></>} />
                    <Route path="account-view/notifications" element={<><div style={{minHeight:'80vh'}}><Notifications /></div></>} />
                    <Route path="account-view/contact" element={<><div style={{minHeight:'80vh'}}><Contact /></div></>} />
                    <Route exact path="account-view/privicy-policy/" element={<PrivicyPolicy/>} />
                    <Route exact path="account-view/about/" element={<About/>} />
                    <Route path="/account/" element={<Account />} />
                    <Route exact path="/cetagory/" element={<><div style={{minHeight:'80vh'}}><ArticlesCetagory cetagoryQuantity="all" articlesQuantity={2} /></div></>} />
                    <Route exact path="/search/:data" element={<><div style={{minHeight:'80vh'}}><Articles searching={true} quantity="all" title="Search Results..." /></div></>} />
                    <Route path="/article/:id/" element={<><div style={{minHeight:'80vh'}}><ArticleView /></div></>} />
                    <Route path="/" index
                      element={
                      <>
                        <MainLanding /><Articles quantity="all" title="Latest Articles" />
                        <ArticlesCetagory cetagoryQuantity={2} articlesQuantity={2} />
                        </>
                      } />

                </Routes>

              </div>
              <Footer />

            </Router>
          </UtilsProvider>
        </SearchProvider>
      </AuthProvider>

    </>
  );
}

export default App;
