import React, { useContext, useEffect,useState } from 'react';
import { AuthContext } from '../Context/AuthContext';
import { UtilsContext } from '../Context/UtilsContext';
import { ContactHandles } from './ContactHandles';
import '../CSS/AccountDetails.css';
import { Link, useNavigate} from 'react-router-dom';
import axios from 'axios'

export const AccountDetails = () => {
    
    const {userData,Login,logoutUser} = useContext(AuthContext)
    const {settingProgressBar,csrfToken} = useContext(UtilsContext)
    const [unseenNoti, setUnseenNoti] = useState(null)
    const nevigate = useNavigate()
    
    useEffect(() => {
        let csrftoken = csrfToken('csrftoken')
        settingProgressBar(100)
        if(Login && userData.id){
            axios.post('/api/notification/', { 'user': userData.id,'work':'unseenQuantity'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{setUnseenNoti(res.data.unseenQuantity)}
            ,(error) => {
                    console.log('Something went wrong //////')
                }
            );
        }
    }, [userData])

    return (
        <div>
            <section className='acc-home' >
                {Login?
                    <div className='acc-main'>
                        <div className='acc-container-1'>
                            <h4 className='display-4 '>{userData.firstName} {userData.lastName}</h4>
                        </div>
                        <div className='acc-container-2' >
                            <div className="cursor-pointer">
                                <Link className='text-decoration-none' to="/account-view/saved-list" >Saved Articles</Link>
                            </div>
                            <hr />
                            <div className="cursor-pointer">
                                <Link className='text-decoration-none' to="/account-view/view-history" >View Articles History</Link>
                            </div>
                            <hr />
                            <div className="cursor-pointer d-flex justify-content-between">
                                <Link className='text-decoration-none' to="/account-view/notifications" >Notifications</Link>
                                {unseenNoti?<strong style={{fontSize:'11px',background:'#e83b8c',border:'2px solid #e83b8c',borderRadius:'12px',textAlign:'center',height:'20px',width:'20px',color:'white'}}>{unseenNoti}</strong>:''}
                            </div>
                            <hr style={{opacity:'1',margin:'40px 0px'}} />
                            <div className="cursor-pointer">
                                <Link className='text-decoration-none text-dark' to="/account-view/contact" >Contact Us</Link>
                            </div>
                            <hr />
                            <div className="cursor-pointer">
                                <Link className='text-decoration-none text-dark' to="/account-view/about" >About</Link>
                            </div>
                            <hr />
                            <div className="cursor-pointer">
                                <Link className='text-decoration-none text-dark' to="/account-view/privicy-policy" >Privicy Policy</Link>
                            </div>
                            <br />
                            <div className="cursor-pointer btn-logout">
                                <button onClick={()=>{logoutUser();nevigate('/',{replace:true})}} className="btn">Logout</button>
                            </div>
                            <br />
                            </div>
                    </div>
                    :
                    <p className='text-center my-4'><strong>You are not login please <Link to="/account">Login</Link>  to view your details.</strong></p>
                }
                <div className="container">
                    <ContactHandles />
                </div>
            </section>
        </div>
)}
