import React, { useEffect,useContext,useState } from 'react'
import {AuthContext} from '../Context/AuthContext'
import {UtilsContext} from '../Context/UtilsContext'
import axios from 'axios'
import { Link } from 'react-router-dom'
import articleImage from '../Vlog_03.png'


export const SavedList = (props) => {

    const [savedListData, setSavedListData] = useState([])
    const {Login,userData} = useContext(AuthContext)
    const {settingProgressBar} = useContext(UtilsContext)

    function getCSRFToken(name) {
        let cookieValue = null;
        if (document.cookie && document.cookie !== '') {
            const cookies = document.cookie.split(';');
            for (let i = 0; i < cookies.length; i++) {
                const cookie = cookies[i].trim();
                // Does this cookie string begin with the name we want?
                if (cookie.substring(0, name.length + 1) === (name + '=')) {
                    cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                    break;
                }
            }
        }
        return cookieValue;
    }

    function truncateString(str, num) {
        if (str.length > num) {
                return str.slice(0, num) + "...";
        } else {
                return str;
        }
    }

    useEffect(() => {
        const csrftoken = getCSRFToken('csrftoken');
        if(Login){
            axios.post('/api/getSavedArticlesOrHistory/', { 'user': userData.id, 'getFrom': 'SavedItems'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{setSavedListData(res.data);settingProgressBar(100);}
            ,(error) => {
                    console.log('Something went wrong in saving article as history')
                }
            );
            
        }
    }, [userData])

    return (
        <div>
            {Login?
            <>
            {savedListData.length>0?<div><h4 style={{ padding: '0px 5%', margin: '24px 0px 0px 0px', fontSize: '20px' }}>{props.title}</h4>
            <hr style={{ width: '90%', backgroundColor: 'balck', margin: '8px auto 12px auto' }} /></div>
            :
            <p className='text-center my-4'><strong>Nothing found in your save list.</strong></p>
            }
            <div className="articlesContainer ">
                    {
                            savedListData.map((article, index) => {
                                    return (
                                            <div key={index} className="mainPageArticle animate__animated animate__fadeInUp">
                                                    <img className="mainPageArticleImage" src={`${!article.picture ? articleImage : article.picture}`} alt="Article" />
                                                    <div className="mainPageArticleContent-1">
                                                            <div className="mainPageArticleContent-2">
                                                                    <p >{truncateString(article.title, 45)}<Link className="pointer" to={`/article/${article.id}/`}> Read Now</Link></p>
                                                            </div>
                                                            <div className="mainPageArticleContent-3">

                                                                    <small>Upload Date: <strong> {new Date(article.pub_date).toLocaleString('en-UK', {
                                                                            day: 'numeric',
                                                                            month: 'numeric',
                                                                            year: 'numeric',
                                                                    })}</strong></small>
                                                                    <small>Written By : <strong>{article.author}</strong></small>

                                                            </div>

                                                    </div>
                                            </div>
                                    )
                            })
                    }

            </div></>
            :
            <p className='text-center my-4'><strong>You are not login please <Link to="/account">Login</Link>  to view your details.</strong></p>}
        </div>
    )
}

SavedList.defaultProps = {
    title: "Saved List...",
}