import React, { useContext, useEffect, useState } from 'react'
import { AuthContext } from '../Context/AuthContext'
import {Link} from 'react-router-dom'
import '../CSS/Contact.css'
import axios from 'axios'
import { UtilsContext } from '../Context/UtilsContext'
import { ContactHandles } from './ContactHandles'


export const Contact = () => {

    const {Login,userData} = useContext(AuthContext)
    const {csrfToken,settingProgressBar} = useContext(UtilsContext)
    const [userName,setUserName] = useState('')

    function contactFormHandler(e){
        let csrftoken = csrfToken('csrftoken') 
        e.preventDefault();
        axios.post('/api/contact/', { 'title': e.target.title.value,'description': e.target.description.value, 'user': userData.id}, {
            headers: {
                'Content-type': 'application/json',
                'X-CSRFToken': csrftoken,
            }
        }).then((res)=>{console.log(res.data)}
        ,(error) => {
                console.log('Something went wrong in saving article as history')
            }
        );
    }

    useEffect(() => {
        if(Login){
            setUserName(userData.username)
            settingProgressBar(100)

        }else{
            settingProgressBar(100)
        }
    }, [userData]);
    

    return (
        <div>
            <div className='acc-contact container my-5'>
            {Login?
            <div>
                <h4 className='text-center display-4 mb-5'>Contact Us</h4>
                    <form onSubmit={(e)=>{e.preventDefault();contactFormHandler(e)}}>
                        <div className="mb-3 d-flex justify-content-between ">
                            <div style={{ marginRight: '2px' }} className="w-100">
                                <input placeholder="Title..." name="title" type="text" className="input-all-unset input-border-bottom w-100" required />
                            </div>
                        </div>
                        <div className="mb-3 w-100">
                            <textarea rows={10} column={14} name="description" placeholder="Detailed Information..." className="form-control" required />
                        </div>
                        <small>W'll try to reply as quick as possible! on this email.</small>
                        <div className="d-flex justify-content-between w-100">
                            <input className='w-50 acc-contact-mail' value={userName|| ''} readOnly />
                            <button type="submit" className="btn acc-submit">Submit</button>
                        </div>
                    </form>
            </div>:<p className='text-center my-4'><strong>You are not login please <Link to="/account">Login</Link>  to Contact Us.</strong></p>}
            <ContactHandles />
            </div>
        </div>
    )
}
