import React, { useContext, useEffect, useState } from 'react'
import '../CSS/ArticleView.css'
import axios from 'axios'
import { UtilsContext } from '../Context/UtilsContext'
import { AuthContext } from '../Context/AuthContext'
import { useParams } from 'react-router-dom'
import Spinner from '../Components/Spinner'
import Comments from './Comments'

function ArticleView(props) {

    const [fetchItem, setFetchItem] = useState([])
    const {settingProgressBar} = useContext(UtilsContext)
    const {Login,userData} = useContext(AuthContext)
    const [savedItem, setSavedItem] = useState(false)
    const [loading, setLoading] = useState(true)
    const prams = useParams()
    
    function getCSRFToken(name) {
        let cookieValue = null;
        if (document.cookie && document.cookie !== '') {
            const cookies = document.cookie.split(';');
            for (let i = 0; i < cookies.length; i++) {
                const cookie = cookies[i].trim();
                // Does this cookie string begin with the name we want?
                if (cookie.substring(0, name.length + 1) === (name + '=')) {
                    cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                    break;
                }
            }
        }
        return cookieValue;
    }

    function saveArticle(){
        const csrftoken = getCSRFToken('csrftoken');
        if(Login){
            axios.post('/api/saveArticlesOrHistory/', { 'article': prams.id, 'user': userData.id, 'saveIn': 'SavedItems'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{(res.data.details).includes('removed')?setSavedItem(false):setSavedItem(true)}
            ,(error) => {
                    console.log('Something went wrong in saving article as history')
                }
            );

        }
    }

    useEffect(() => {
        const csrftoken = getCSRFToken('csrftoken');
        settingProgressBar(25);
        if(Login){
            axios.post('/api/saveArticlesOrHistory/', { 'article': prams.id, 'user': userData.id, 'saveIn': 'History'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{}
            ,(error) => {
                    console.log('Something went wrong in saving article as history')
                }
            );

            axios.post('/api/getSavedArticlesOrHistory/', { 'article': prams.id, 'user': userData.id, 'getFrom': 'oneArticle'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{(res.data.length)>0?setSavedItem(true):setSavedItem(false)}
            ,(error) => {
                    console.log('Something went wrong in fetching saved Articles')
                }
            );

        }

        function fetchArticles() {
            try {
                axios.get(`/api/article/${prams.id}`)
                    .then(res => { setFetchItem(res.data);settingProgressBar(100);setLoading(false)})
            }
            catch{
                console.log('error')
            }
        }
        fetchArticles();
    }, [])
    

    return (
        <div>
            <div className="container">
                <div className="my-4 p-3">
                    {loading?<Spinner/>:<><h4 className='mt-3 mb-3'>{fetchItem.title}</h4>
                    <div id='mainArticle' dangerouslySetInnerHTML={{__html: `${fetchItem.description}`}} />
                    <br />
                    <small>Upload Date: <strong> {new Date(fetchItem.pub_date).toLocaleString('en-UK', {
                        day: 'numeric',
                        month: 'numeric',
                        year: 'numeric',
                    })}</strong></small></>}
                    <br />
                    <small>Written By : <strong>{fetchItem.author}</strong></small>
                    <br />
                    <br />
                    {Login?<strong onClick={()=>saveArticle()} className='pointer'>Save this Article {savedItem?<i className='fas fa-bookmark' style={{fontSize:"24px"}}></i>:<i className='far fa-bookmark' style={{fontSize:"24px"}} ></i>}</strong>
                    :
                    <div className='d-flex flex-column'>
                        <small><strong>Login Required!</strong></small>
                        <strong style={{opacity:'0.5'}}>Save this Article <i className='far fa-bookmark' style={{fontSize:"24px"}} ></i></strong></div>
                    } 
                    <Comments />
                </div>
            </div>
        </div>
    )
}

export default ArticleView
