import '../CSS/Articles.css'
import articleImage from '../Vlog_03.png'
import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Spinner from './Spinner'
import InfiniteScroll from "react-infinite-scroll-component";
import { SearchContext } from '../Context/SearchContext'
import { UtilsContext } from '../Context/UtilsContext'


export default function Articles(props) {

        const [fromNext, setFromNext] = useState(0)
        const { searchData , search } = useContext(SearchContext)
        const { settingProgressBar } = useContext(UtilsContext)
        const [articlesData, setArticlesData] = useState([])
        const [contentEndText, setContentEndText] = useState('Yay! You have seen it all')
        const [haveArticles, setHaveArticle] = useState(true)


        const checkDataEnd = (data) => {
                if (data.length === 0) {
                        setHaveArticle(false)
                }
        }

        function fetchArticles() {
                try {
                        axios.get(`/api/articles-list?cetagory=${props.cetagory}&articlesQty=${props.quantity}&fromNext=${fromNext}`)
                                .then(res => { !props.quantity === 'all' && ((res.data[0]).total_articles_length <= props.quantity ? setHaveArticle(false) : setHaveArticle(true)); setArticlesData(res.data); settingProgressBar(100); setFromNext(fromNext + props.quantity) })
                }
                catch {
                        console.log('Error')
                }
        }

        const fetchData = async () => {
                try {
                        axios.get(`/api/articles-list?cetagory=${props.cetagory}&articlesQty=${props.quantity}&fromNext=${fromNext}`)
                                .then(res => { ((res.data[0]).total_articles_length > fromNext ? setHaveArticle(false) : setHaveArticle(true)); setArticlesData(articlesData.concat(res.data)); setFromNext(fromNext + props.quantity); checkDataEnd(res.data) })
                }
                catch {
                        console.log('Error')
                }
        };

        useEffect(() => {
                settingProgressBar(25);
                if (!props.searching) {
                        setContentEndText('Yay! You have seen it all')
                        fetchArticles();
                }
                else {
                        search()
                }
                if (props.quantity === 'all') {
                        setHaveArticle(false)
                        setContentEndText('')
                }
        }, [])

        useEffect(() => {
                if(props.searching){
                        if(!searchData.length > 0){
                                setContentEndText('Query Not Found! Please Use Short Keywords For Better Results')
                        }else{
                                setContentEndText('Yay! You have seen it all')
                        }
                }
        }, [searchData])

        useEffect(() => {
                settingProgressBar(100)
        }, [])

        function truncateString(str, num) {
                if (str.length > num) {
                        return str.slice(0, num) + "...";
                } else {
                        return str;
                }
        }

        return (
                <>
                        <div>
                                <h4 style={{ padding: '0px 5%', margin: '24px 0px 0px 0px', fontSize: '20px' }}>{props.title}</h4>
                                <hr style={{ width: '90%', backgroundColor: 'balck', margin: '8px auto 12px auto' }} />
                                <div className="articlesContainer ">
                                        <InfiniteScroll
                                                dataLength={props.searching ? searchData.length : articlesData.length}
                                                next={fetchData}
                                                hasMore={haveArticles}
                                                loader={<Spinner />}
                                                endMessage={
                                                        <p style={{ textAlign: 'center', width: '100%', margin: '6px' }}>
                                                                <b>{contentEndText}</b>
                                                        </p>
                                                }
                                        >
                                                {
                                                        (props.searching ? searchData : articlesData).map((article, index) => {
                                                                return (
                                                                        <div key={index} className="mainPageArticle animate__animated animate__fadeInUp">
                                                                                <img className="mainPageArticleImage" src={`${!article.picture ? articleImage : article.picture}`} alt="Article" />
                                                                                <div className="mainPageArticleContent-1">
                                                                                        <div className="mainPageArticleContent-2">
                                                                                                <p >{truncateString(article.title, 45)}<Link className="pointer" to={`/article/${article.id}/`}> Read Now</Link></p>
                                                                                        </div>
                                                                                        <div className="mainPageArticleContent-3">

                                                                                                <small>Upload Date: <strong> {new Date(article.pub_date).toLocaleString('en-UK', {
                                                                                                        day: 'numeric',
                                                                                                        month: 'numeric',
                                                                                                        year: 'numeric',
                                                                                                })}</strong></small>
                                                                                                <small>Written By : <strong>{article.author}</strong></small>

                                                                                        </div>

                                                                                </div>
                                                                        </div>
                                                                )
                                                        })
                                                }
                                        </InfiniteScroll>

                                </div>
                        </div>
                </>
        )
}

Articles.defaultProps = {
        quantity: 5,
        cetagory: "all",
        title: "Articles",
        searching: false,
}
