import React from 'react'
import '../CSS/MainLanding.css'
import mainPicture from '../Vlog_03.png'
import {Link} from 'react-router-dom'

export default function MainLanding() {

    const vectorMainPage = mainPicture

    return (
        <>
        <div id="landPage">
            <div id="mainGreeting">
                <h1 className="mainH1">Our Motive</h1>
                <h4 className="mainH4">Provide You Best Content and Intresting Articles Ever.</h4>
                <p id="mainPara">Quality and Reacently uploaded Content is Avilable on Intrest Blog.</p>
                <button id="mainExploreBtn" className="pointer"><Link to="/articles/">Explore More</Link></button>
            </div>
            <div id="mainIlParent">
                <div id="mainIl" style={{background: `url(${vectorMainPage})center center/cover`}}></div>
            </div>
        </div>
        </>
    )
}
