import React, { useContext, useEffect,useState } from 'react'
import axios from 'axios'
import { UtilsContext } from '../Context/UtilsContext'
import { AuthContext } from '../Context/AuthContext'
import '../CSS/Notifications.css'

export const Notifications = () => {

    const {csrfToken,settingProgressBar} = useContext(UtilsContext)
    const {Login,userData} = useContext(AuthContext)
    const [notiData, setNotiData] = useState([])

    useEffect(() => {
        let csrftoken = csrfToken('csrftoken')
        function updateSeen(data) {
            let array = []
            for (let i = 0; i < data.length; i++) {
                const element = data[i];
                if(element.seen_by_user===false){
                    array.push(element.id)
                }
            }
            axios.post('/api/notification/', { 'user': userData.id , 'work':'update','updateItems':array}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{}
            ,(error) => {
                    console.log('Something went wrong //////')
                }
            );
            
        }
        if(Login){
            axios.post('/api/notification/', { 'user': userData.id,'work':'fetch'}, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res)=>{setNotiData(res.data);settingProgressBar(100);updateSeen(res.data)}
            ,(error) => {
                    console.log('Something went wrong //////')
                }
            );
        }else{
            settingProgressBar(100)
        }

    }, [userData])

    return (
        <div className='container my-3 noti-main-container'>
            {notiData.length > 0 ?<h4 className='text-center display-4 my-4'>Notifications</h4>:<p className='text-center my-1'><strong>Notifications is empty!</strong></p>}
            {notiData.map((data,index)=>{
                return(
                        <div className={`noti-box my-2 p-2 ${data.seen_by_user === false?'noti-box-before':''}`} key={index}>
                            <div className='noti-outer'><i className='noti-title-head'>Title:</i> <p>{data.title}</p></div>
                            <div className='noti-outer'><i className='noti-desc-head'>Description:</i> <p>{data.description}</p></div>
                            <div className='noti-outer'><strong className='w-100 d-flex flex-wrap'><i className='noti-replay-head'>Replay:</i><p>{data.replay}</p></strong></div>
                            <br />
                            <small>Date: <strong> {new Date(data.date).toLocaleString('en-UK', {
                                    day: 'numeric',
                                    month: 'numeric',
                                    year: 'numeric',
                            })}</strong></small>
                            <br />
                        </div>
                
                )
            
            })}
        

        </div>
    )
}
