import '../CSS/Articles.css'
import articleImage from '../Vlog_03.png'
import axios from 'axios'
import React, { useContext, useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import Spinner from './Spinner'
import { UtilsContext } from '../Context/UtilsContext'

export default function ArticlesCetagory(props) {

        const {settingProgressBar} = useContext(UtilsContext)
        const [articlesData, setArticlesData] = useState([])
        const [checkCetagory, setCheckCetagory] = useState([])
        const [fetching, setFetching] = useState(true)
        const [itemQuantity, setItemQuantity] = useState([])
        const [fromNext, setFromNext] = useState({})
        const [spinner, setSpinner] = useState(false)
        const [searchingItem, setSearchingItem] = useState('')
        
        function settingCetagory(data) {
                let arrayOfCetagory = []
                let arrayOfQuantity = []
                for (let item = 0; item < data.length; item++) {
                        const element = data[item];
                        if (arrayOfCetagory[(arrayOfCetagory.length) - 1] !== element.cetagory) {
                                arrayOfCetagory.push(element.cetagory)
                                arrayOfQuantity.push(element.total_articles_length)
                        }
                }
                setCheckCetagory(arrayOfCetagory)
                setItemQuantity(arrayOfQuantity)
        }

        function fetchMoreData(item,index){

                let items = {...fromNext}
                items[item]===undefined?items[item]=((props.articlesQuantity)):items[item]=(items[item]+props.articlesQuantity)
                setFromNext(items)
                try {
                        axios.get(`/api/articles-list/cetagory?cetagoriesQty=${props.cetagoryQuantity}&articlesQty=${(items[item]+props.articlesQuantity)}&fromNext=${items[item]}&cetagoryIs=${item}`)
                                .then(res => {setArticlesData(articlesData.concat(res.data));setSpinner(false);setSearchingItem('')})
                }
                catch {
                        console.log('Error')
                }

                

        }

        useEffect(() => {
                function fetchArticles() {
                        try {
                                axios.get(`/api/articles-list/cetagory?cetagoriesQty=${props.cetagoryQuantity}&articlesQty=${props.articlesQuantity}&fromNext=${0}`)
                                        .then(res => { setArticlesData(res.data); settingCetagory(res.data); setFetching(false);settingProgressBar(100)})
                        }
                        catch {
                                console.log('Error')
                        }
                }
                settingProgressBar(25);
                fetchArticles();
        }, [])

        
        function truncateString(str, num) {
                if (str.length > num) {
                        return str.slice(0, num) + "...";
                } else {
                        return str;
                }
        }

        return (
                <>
                        <div>
                                {fetching ? <Spinner /> : ''}
                                {(checkCetagory).map((item, index) => {
                                        return (
                                                <div key={index}>
                                                        <h6 style={{ padding: '0px 5%', margin: '24px 0px 0px 0px', fontSize: '12px' }}>{item}</h6>
                                                        <hr style={{ width: '90%', backgroundColor: 'balck', margin: '8px auto 12px auto' }} />
                                                        <div className="articlesContainer">
                                                                <div style={{ maxWidth: '1300px', display: 'flex', alignItems: 'center', justifyContent: 'center', flexWrap: 'wrap' }}>
                                                                        {
                                                                                articlesData.map((article, index) => {
                                                                                        if (article.cetagory === item) {
                                                                                                return (
                                                                                                        <div key={index} className="mainPageArticle">
                                                                                                                <img className="mainPageArticleImage" src={`${!article.picture ? articleImage : article.picture}`} alt="Article" />
                                                                                                                <div className="mainPageArticleContent-1">
                                                                                                                        <div className="mainPageArticleContent-2">
                                                                                                                                <p >{truncateString(article.title, 45)}<Link to={`/article/${article.id}/`}> Read Now</Link></p>
                                                                                                                        </div>
                                                                                                                        <div className="mainPageArticleContent-3">

                                                                                                                                <small>Upload Date: <strong> {new Date(article.pub_date).toLocaleString('en-UK', {
                                                                                                                                        day: 'numeric',
                                                                                                                                        month: 'numeric',
                                                                                                                                        year: 'numeric',
                                                                                                                                })}</strong></small>
                                                                                                                                <small>Written By : <strong>{article.author}</strong></small>

                                                                                                                        </div>

                                                                                                                </div>
                                                                                                        </div>
                                                                                                )

                                                                                        }
                                                                                        else{
                                                                                                return('')
                                                                                        }
                                                                                })
                                                                        }

                                                                </div>
                                                        </div>
                                                        {spinner && searchingItem === item?<Spinner/>:''}
                                                        {itemQuantity[index] > (fromNext[item]===undefined?props.articlesQuantity:(fromNext[item]+props.articlesQuantity)) ? <div onClick={()=>{fetchMoreData(item,index);setSpinner(true);setSearchingItem(item)}} className="d-flex justify-content-center align-items-center show-more-items-outer">
                                                                <div className="w-100 text-center show-more-items">Show more Items</div>
                                                        </div> : ''}
                                                </div>

                                        )



                                })}
                        </div>
                </>
        )
}


