import React, { useState, createContext} from 'react'
import axios from 'axios'

export const SearchContext = createContext();


export const SearchProvider = (props) => {

    const [articlesData, setArticlesData] = useState([])
    const [searchQuery, setSearchQuery] = useState('')


    const searchData = (e) => {
        if (e) {
            e.preventDefault();
            setSearchQuery(e.target.searchText.value)
            try {
                axios.get(`/api/search/${e.target.searchText.value}`)
                    .then(res => { setArticlesData(res.data) })
            }
            catch {
                console.log('Error')
            }
        } else {
            setSearchQuery(window.location.pathname)
            try {
                axios.get(`/api/${window.location.pathname}`)
                    .then(res => { setArticlesData(res.data)})
            }
            catch {
                console.log('Error')
            }
        }

    }

    const ContextData = {
        search: searchData,
        searchData: articlesData,
        searchQuery: searchQuery,

    }

    return (
        <SearchContext.Provider value={ContextData}>
            {props.children}
        </SearchContext.Provider>
    )
}