import React, { useState, createContext, useEffect } from 'react';
import axios from 'axios';
import jwt_decode from 'jwt-decode'

export const AuthContext = createContext();

export const AuthProvider = (props) => {

    const [Login, setLogin] = useState(false)
    const [userData, setUserData] = useState(false)
    const [authTokens, setAuthTokens] = useState(() => localStorage.getItem("authToken")?JSON.parse(localStorage.getItem("authToken")) : null)
    const [loading, setLoading] = useState(false)
    const [authMassageText, setAuthMassageText] = useState(null);


    function getCSRFToken(name) {
        let cookieValue = null;
        if (document.cookie && document.cookie !== '') {
            const cookies = document.cookie.split(';');
            for (let i = 0; i < cookies.length; i++) {
                const cookie = cookies[i].trim();
                // Does this cookie string begin with the name we want?
                if (cookie.substring(0, name.length + 1) === (name + '=')) {
                    cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                    break;
                }
            }
        }
        return cookieValue;
    }

    const updateToken = () => {
        const csrftoken = getCSRFToken('csrftoken');
        axios.post('/api/token/refresh/', { 'refresh': authTokens }, {
            headers: {
                'Content-type': 'application/json',
                'X-CSRFToken': csrftoken,
            }
        }).then((response) => {
            if (response.status === 200) {
                setLogin(true)
                setAuthTokens(response.data.refresh)
                let array = {}
                let decoded_data = jwt_decode(response.data.access)
                array.username = decoded_data.username
                array.firstName = decoded_data.first_name
                array.lastName = decoded_data.last_name
                array.id = decoded_data.id
                setUserData(array)
                localStorage.setItem('authToken', JSON.stringify(response.data.refresh))

            } else {
                setUserData(false)
                localStorage.removeItem('authToken')
                setLogin(false)
                setAuthTokens('')
            }
        }, (error) => {
            setUserData(false)
            localStorage.removeItem('authToken')
            setLogin(false)
            setAuthTokens('')
            console.log('something went wrong in updating')
        }
        )
        if (loading) {
            setLoading(false)
        }
    }

    const loginUser = (e) => {
        e.preventDefault()
        const csrftoken = getCSRFToken('csrftoken');
        axios.post('/api/token/', { 'username': e.target.username.value, 'password': e.target.password.value }, {
            headers: {
                'Content-type': 'application/json',
                'X-CSRFToken': csrftoken,
            }
        }).then((response) => {
            if (response.status === 200) {
                setLogin(true)
                setAuthTokens(response.data.refresh)
                let array = {}
                let decoded_data = jwt_decode(response.data.access)
                array.username = decoded_data.username
                array.firstName = decoded_data.first_name
                array.lastName = decoded_data.last_name
                array.id = decoded_data.id
                setUserData(array)
                localStorage.setItem('authToken', JSON.stringify(response.data.refresh))

            }
        }, (error) => {
            setAuthMassageText('Error: Please input correct details.')
            console.log('something went wrong in login');
        });
    }

    const signupUser = (e) => {
        e.preventDefault()
        const csrftoken = getCSRFToken('csrftoken');
        axios.post('/api/create-user/', { 'first_name': e.target.firstName.value, 'last_name': e.target.lastName.value, 'username': e.target.email.value, 'password': e.target.password.value, 'email': e.target.email.value }, {
            headers: {
                'Content-type': 'application/json',
                'X-CSRFToken': csrftoken,
            }
        }).then((response) => {
            if (response.data.response === 201) {
                axios.post('/api/token/', { 'username': response.data.loginDetails.email, 'password': response.data.loginDetails.password }, {
                    headers: {
                        'Content-type': 'application/json',
                        'X-CSRFToken': csrftoken,
                    }
                }).then((response) => {
                    setLogin(true)
                    setAuthTokens(response.data.refresh)
                    let array = {}
                    array.username = jwt_decode(response.data.access).username
                    setUserData(array)
                    localStorage.setItem('authToken', JSON.stringify(response.data.refresh))
                })
            }else if(response.data.response ===406){
                setAuthMassageText('Error: User Already Exist')
            }else if(response.data.response === 405){
                setAuthMassageText('Error: Password is too short.')
            }
        
        }
            , (error) => {
                console.log('something went wrong in signup')
            });
    }

    function logoutUser() {
        setUserData(false)
        localStorage.removeItem('authToken')
        setLogin(false)
        setAuthTokens('')
    }

    useEffect(() => {

        let forteenMinutes = 1000 * 60 * 14
        if (authTokens) {
            if(!Login){
                updateToken()
            }
            let interval = setInterval(() => {
                if (authTokens) {
                    updateToken()
                }
            }, forteenMinutes)
            return () => clearInterval(interval)
        } else {
            setLoading(false)
        }

    }, [loading, authTokens])

    const ContextData = {
        Login: Login,
        loginUser: loginUser,
        userData: userData,
        signupUser: signupUser,
        logoutUser:logoutUser,
        authMassageText:authMassageText,
        setAuthMassageText,setAuthMassageText

    }

    return (
        <AuthContext.Provider value={ContextData}>
            {loading ? null : props.children}
        </AuthContext.Provider>
    )
}