import React, { useContext, useEffect, useState } from 'react'
import '../CSS/Account.css'
import LoginImage from '../loginIll.jpg'
import SignupImage from '../signupIll.jpg'
import 'animate.css';
import { AuthContext } from '../Context/AuthContext';
import { UtilsContext } from '../Context/UtilsContext'
import { useNavigate } from 'react-router-dom';

const Account = () => {

    const {settingProgressBar} = useContext(UtilsContext)
    const [PageActiveComp, setPageActiveComp] = useState('Login')
    const [LoginLineWidth, setLoginLineWidth] = useState('100%')
    const [SignUpLineWidth, setSignUpLineWidth] = useState('2%')
    const [animation, setAnimation] = useState('animate__fadeInUp')
    const {loginUser,signupUser,Login,authMassageText,setAuthMassageText} = useContext(AuthContext)
    const nevigate = useNavigate()

    const setLoginLine = () => {
        setLoginLineWidth('100%');
        setSignUpLineWidth('2%');
        setPageActiveComp('Login');
        setAnimation('animate__fadeInUp')
    }
    const setSignupLine = () => {
        setSignUpLineWidth('100%');
        setLoginLineWidth('2%');
        setPageActiveComp('SignUp');
        setAnimation('animate__fadeInUp')
    }

    useEffect(() => {
        let interval = setTimeout(() => {
            setAnimation('')
        }, 1000);
        return ()=> clearInterval(interval)
    }, [animation])

    useEffect(() => {
        settingProgressBar(100)
    }, [])

    useEffect(() => {
        if(Login){
            nevigate('/account-view',{replace:true})
        }
    }, [Login])

    useEffect(() => {
        setTimeout(() => {
            setAuthMassageText(null)
        }, 15000);
    }, [authMassageText]);
    


    return (
        <>
            <div id="massage">
                    {authMassageText?<div className="alert alert-danger alert-dismissible fade show my-2 mx-2" role="alert">
                        {authMassageText}
                        <button type="button" className="btn-close" data-bs-dismiss="alert" aria-label="Close" onClick={()=>setAuthMassageText(null)}></button>
                    </div>:''}
            </div>
            <div>
                <div className='d-flex justify-content-evenly mt-5'>
                    <div>
                        <h4 className='acc-login-head pointer' onClick={() => setLoginLine()}>Login Now</h4>
                        <hr style={{ width: LoginLineWidth }} className='acc-active-page-line-1' />
                        <hr style={{ margin: '4px 0px' }} />
                    </div>
                    <div>
                        <h4 className='acc-sign-up-head pointer ' onClick={() => setSignupLine()}>Sign Up</h4>
                        <hr style={{ width: SignUpLineWidth }} className='acc-active-page-line-2' />
                        <hr style={{ margin: '-8px 0px' }} />
                    </div>
                </div>
            </div>
            <div style={{ height: '100px' }}></div>
            <div className="mx-4 main-container">
                <div className={`my-4 acc-ill`}>
                    <img src={PageActiveComp === 'Login' ? LoginImage : SignupImage} className={`img-fluid animate__animated animate__faster ${animation} `} alt={PageActiveComp === 'Login' ? 'Login' : 'SignUp'} />
                </div>

                <div className='acc-form'>
                    {/* Login from */}
                    {PageActiveComp === 'Login' ?
                        <form onSubmit={loginUser} className={`w-100 ${PageActiveComp === 'Login' ? 'animate__animated animate__fadeInUp' : ''}`} >
                            <div className="mb-3">
                                <label htmlFor="login-email" className="form-label">Email address</label>
                                <input type="email" name="username" className="form-control" id="login-email" aria-describedby="login-emailHelp" required />
                                <div id="login-emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                            </div>
                            <div className="mb-3 w-100">
                                <label htmlFor="login-password" className="form-label">Password</label>
                                <input type="password" name="password" className="form-control" id="login-password" required />
                            </div>
                            <div className="d-flex justify-content-end w-100">
                                <button type="submit" className="btn acc-submit">Submit</button>
                            </div>
                        </form>

                        :

                        <div className='acc-signup'>
                            <form onSubmit={signupUser} className={`w-100 ${PageActiveComp === 'SignUp' ? 'animate__animated animate__fadeInUp' : ''}`}>
                                <div className="mb-3 d-flex justify-content-between ">
                                    <div style={{ marginRight: '2px' }} className="w-100">
                                        <label htmlFor="signup-first-name" className="form-label">First Name</label>
                                        <input placeholder="First Name" name="firstName" type="text" className="form-control " id="signup-first-name" required />
                                    </div>
                                    <div style={{ marginLeft: '2px' }} className="w-100">
                                        <label htmlFor="signup-last-name" className="form-label">Last Name</label>
                                        <input placeholder="Last Name" name="lastName" type="text" className="form-control " id="signup-last-name" required />
                                    </div>
                                </div>
                                <div className="mb-3">
                                    <label htmlFor="signup-email" className="form-label">Email address</label>
                                    <input type="email" placeholder="example@gmail.com" name="email" className="form-control" id="signup-email" aria-describedby="signup-emailHelp" required />
                                    <div id="signup-emailHelp" className="form-text">We'll never share your email with anyone else.</div>
                                </div>
                                <div className="mb-3 w-100">
                                    <label htmlFor="signup-password" className="form-label">Password</label>
                                    <input type="password" name="password" placeholder="Password" className="form-control" id="signup-password" required />
                                </div>
                                <div className="mb-3 w-100">
                                    <label htmlFor="signup-confirm-password" className="form-label">Confirm Password</label>
                                    <input type="password" name="confirmPassword" placeholder="Confirm Password" className="form-control" id="signup-confirm-password" required />
                                </div>
                                <div className="d-flex justify-content-end w-100">
                                    <button type="submit" className="btn acc-submit">Submit</button>
                                </div>
                            </form>
                        </div>
                    }


                </div>
            </div>
            <div style={{ height: '30vh' }}></div>
        </>
    )
}

export default Account

