import React, { useState, useContext } from 'react'
import { NavLink, useNavigate } from 'react-router-dom'
import '../CSS/Navbar.css'
import { AuthContext } from '../Context/AuthContext'
import { SearchContext } from '../Context/SearchContext'
import LoadingBar from 'react-top-loading-bar'
import { UtilsContext } from '../Context/UtilsContext'


const Navbar = () => {
    
    const {progressBar} = useContext(UtilsContext)
    const { userData } = useContext(AuthContext)
    const { search } = useContext(SearchContext)
    const [stripeOneTransform, setStripeOneTransform] = useState()
    const [stripeTwoWidth, setStripeTwoWidth] = useState()
    const [stripeThreeTransform, setStripeThreeTransform] = useState()
    const [searchQuery, setSearchQuery] = useState('')
    const [meanuOpen, setMeanuOpen] = useState(false)
    const navigate = useNavigate()

    function openMeanu() {
            setStripeTwoWidth('0px');
            setStripeOneTransform('rotate(45deg) translateY(6px) translateX(7px)');
            setStripeThreeTransform('rotate(135deg) translateY(5px) translateX(-5px)');
            setMeanuOpen(true);
    }

    function closeMeanu() {
            setStripeOneTransform('');
            setStripeTwoWidth('20px');
            setStripeThreeTransform('');
            setMeanuOpen(false)
    }



    return (
        <>
            <div>
                <LoadingBar
                    color='#ff7b65'
                    progress={progressBar}
                />
            </div>
            <nav id="navBar">
                <div className="d-flex justify-content-center align-items-center text-white">
                    <div id="brandImage">
                        <img src="/media/blogImage.png" alt="Blog-Logo" />
                    </div>
                    <h4 id="brandName">Bilgi</h4>
                </div>
                <div>
                    <ul id="navBarItems" className="">
                    <li className="pointer navLinks" ><NavLink to="/" >Home</NavLink></li>
                        <li className="pointer navLinks" ><NavLink to="/articles/" className={(window.location.pathname).includes('/article')?'active':''}>Articles</NavLink></li>
                        <li className="pointer navLinks" ><NavLink to="/cetagory/" >Cetagories</NavLink></li>
                        {!userData ? <button className="pointer navLinks auth-btn" ><NavLink to="/account/" >Login</NavLink></button> : <li className="pointer navLinks" ><NavLink className={(window.location.pathname).includes('/account-view')?'active':''} to="/account-view/">Account Details</NavLink></li>}
                        <form onSubmit={(e) => { e.preventDefault(); search(e); navigate(`/search/${searchQuery}`) }} className="d-flex ms-2">
                            <input onChange={(e) => setSearchQuery(e.target.value)} style={{ all: 'unset' }} className="navSearch" name="searchText" placeholder="Search" aria-label="Search" />
                        </form>
                    </ul>
                    <div onClick={() =>!meanuOpen ? openMeanu() : closeMeanu()} data-bs-toggle="collapse" href="#hamBurgerMeanu" role="button" aria-expanded="false" aria-controls="hamBurgerMeanu" id="hamBurger">
                        <hr id="stripe-1" style={{ transform: stripeOneTransform }} className="stripes" />
                        <hr id="stripe-2" style={{ width: stripeTwoWidth }} className="stripes" />
                        <hr id="stripe-3" style={{ transform: stripeThreeTransform }} className="stripes" />
                    </div>
                </div>
            </nav>
            <div>
                <ul className='collapse' id='hamBurgerMeanu' >
                    <li className="pointer navLinks" ><NavLink to="/" >Home</NavLink></li>
                    <li className="pointer navLinks" ><NavLink to="/articles/" className={(window.location.pathname).includes('/article')?'active':''}>Articles</NavLink></li>
                    <li className="pointer navLinks" ><NavLink to="/cetagory/" >Cetagories</NavLink></li>
                    {!userData ? <li className="pointer navLinks" ><button className="pointer navLinks auth-btn" ><NavLink to="/account/" >Login</NavLink></button></li> : <li className="pointer navLinks" ><NavLink className={(window.location.pathname).includes('/account-view')?'active':''} to="/account-view/">Account Details</NavLink></li>}
                    <li ><form onSubmit={(e) => { e.preventDefault(); search(e); navigate(`/search/${searchQuery}`) }} className="d-flex ms-2">
                        <input onChange={(e) => setSearchQuery(e.target.value)} style={{ all: 'unset' }} className="navSearch" name="searchText" placeholder="Search" aria-label="Search" />
                    </form></li>
                </ul>
            </div>
        </>
    )
}

export default Navbar;
