import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { AuthContext } from '../Context/AuthContext'
import { UtilsContext } from '../Context/UtilsContext'
import '../CSS/Comments.css'
import profile_avtar from '../profile_avtar.png'
import axios from 'axios'

const Comment = () => {

    const { Login, userData } = useContext(AuthContext)
    const { csrfToken } = useContext(UtilsContext)
    const [comments, setComments] = useState([]);

    const prams = useParams()

    function comment(e) {
        let csrftoken = csrfToken('csrftoken');
        if (Login) {
            axios.post('/api/save-comments/', { 'work': 'comments', 'article': prams.id, 'user': userData.id, 'commentData': e.target.comment.value }, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res) => { setComments(res.data); e.target.comment.value = '' }
                , (error) => {
                    console.log('Something went wrong //////')
                }
            );
        }
    }

    function replay_comment(e, mainComment) {
        let csrftoken = csrfToken('csrftoken');
        if (Login) {
            axios.post('/api/save-comments/', { 'work': 'replay_comments', 'article': prams.id, 'user': userData.id, 'commentData': e.target.comment.value, 'main-comment': mainComment }, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res) => { setComments(res.data); e.target.comment.value = '' }
                , (error) => {
                    console.log('Something went wrong //////')
                }
            );
        }
    }

    function delete_comment(commentToDel) {
        let csrftoken = csrfToken('csrftoken');
        if (Login) {
            axios.post('/api/delete-comments/', { 'work': 'del_comment', 'article': prams.id, 'user': userData.id, 'commentToDel': commentToDel }, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res) => { setComments(res.data) }
                , (error) => {
                    console.log('Something went wrong //////')
                }
            );
        }
    }

    function delete_replay_comment(mainComment, commentToDel) {
        let csrftoken = csrfToken('csrftoken');
        if (Login) {
            axios.post('/api/delete-comments/', { 'work': 'del_replay_comment', 'article': prams.id, 'user': userData.id, 'replayToDel': commentToDel, 'main-comment': mainComment }, {
                headers: {
                    'Content-type': 'application/json',
                    'X-CSRFToken': csrftoken,
                }
            }).then((res) => { setComments(res.data) }
                , (error) => {
                    console.log('Something went wrong //////')
                }
            );
        }
    }



    useEffect(() => {
        let csrftoken = csrfToken('csrftoken');
        axios.post('/api/get-comments/', { 'work': 'comments', 'article': prams.id }, {
            headers: {
                'Content-type': 'application/json',
                'X-CSRFToken': csrftoken,
            }
        }).then((res) => { setComments(res.data) }
            , (error) => {
                console.log('Something went wrong //////')
            }
        );
    }, [userData])

    return (
        <div className='comments-main mt-4 '>
            <div className='comments-form-outer '>
                <form style={{ opacity: !Login ? '0.7' : '1' }} onSubmit={(e) => { e.preventDefault(); comment(e) }} className='w-100 d-flex '>
                    <input disabled={!Login} required name='comment' placeholder={Login ? 'Comment...' : 'Login Rquired to Comment...'} className='comments-input' type="text" />
                    <button type='submit' className='comments-input-btn'><i className="material-icons" style={{ fontSize: '22px' }}>send</i></button>
                </form>
            </div>
            <div className='container mt-4 comments-outer'>

                {comments.map((comment, index) => {
                    return (
                        <div key={index}>
                            <div className='comment-outer my-2'>
                                <div className='comment-image-outer'>
                                    <img className='comment-image' src={profile_avtar} alt="Profile" />
                                </div>
                                <div className='comment'>
                                    <div className='comment-text-outer mb-1'>
                                        <div className="comment-username"><strong>{comment.User_first_name} {comment.User_last_name}</strong></div>
                                        <div className="comment-text">{comment.comment}</div>
                                    </div>
                                    <hr className='comment-break-hr' />
                                    <div className='comment-replay-date'>
                                        <div className="comment-replay" onClick={() => { document.getElementById(`form-of-comment-${comment.id}`).style.display = 'flex' }}><small><i className="fa fa-reply"></i> Replay</small></div>
                                        {comment.User === userData.id ? <div className='me-4 pointer' onClick={() => { delete_comment(comment.id) }}><i style={{color:'red'}} className='far fa-trash-alt'> Delete</i></div> : ''}
                                        <div className="comment-date"><small>4 days ago</small></div>
                                    </div>
                                </div>
                            </div>
                            <form style={{ opacity: !Login ? '0.6' : '1' }} id={`form-of-comment-${comment.id}`} onSubmit={(e) => { e.preventDefault(); replay_comment(e, (comment.id)) }} className='w-100 replay-forms animate__animated animate__lightSpeedInRight  '>
                                <input required disabled={!Login} name='comment' placeholder={Login ? 'Replay Comment...' : 'Login Rquired to Replay Comment...'} className='comments-input' type="text" />
                                <button type='submit' className='comments-input-btn'><i className="material-icons" style={{ fontSize: '22px' }}>send</i></button>
                            </form>

                            {comment.replay_comment_text?.map((replay, replay_index) => {
                                return (
                                    <div key={replay_index} className='ms-5 my-4'>
                                        <div className='comment-outer '>
                                            <div className='comment-image-outer'>
                                                <img className='comment-image' src={profile_avtar} alt="Profile" />
                                            </div>
                                            <div className='comment'>
                                                <div className='comment-text-outer'>
                                                    <div className="comment-username"><strong>{replay.User_first_name} {replay.User_last_name}</strong></div>
                                                    <div className="comment-text">{replay.replay_comment_text}</div>
                                                </div>
                                                <hr className='comment-break-hr' />
                                                <div className='comment-replay-date'>
                                                    {replay.User_id === userData.id ? <div className='me-4' onClick={() => delete_replay_comment(comment.id, replay.replay_comment_text)}><i style={{color:'red'}} className='far fa-trash-alt '> Delete</i></div> : ''} 
                                                    <div className="comment-date me-4"><small>4 days ago</small></div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}

                        </div>
                    )
                })}




            </div>
        </div>
    )
}

export default Comment
